/** @format */

// convenience methods for dealing with holidays

export const isHoliday = (date, year, holidays) => {
  try {
    if (holidays && holidays.years) {
      const yr = year + "";

      if (yr in holidays.years) {
        if (date in holidays.years[yr]) {
          return true;
        }
      }
    }
  } catch (err) {
    console.log("Error in isHoliday():");
    console.log(err.message);
  }
  return false;
};

export const getHolidayName = (date, year, holidays) => {
  if (!isHoliday(date, year, holidays)) {
    return "";
  }

  const yr = year + "";
  return holidays.years[yr][date];
};
